<template>
    <div id="TodoList">
        <!-- ËæìÂÖ•Ê°Ü -->
        <el-input type="text" v-model="inputText">
            <el-badge slot="prefix" class="mark" :value="dataList.length" />
            <el-tooltip class="item" slot="append" effect="dark" content="Êèê‰∫§‰Ω†ÁöÑÂÜÖÂÆπÁîüÊàê TodoList" placement="top-end">
                <el-button type="primary" @click="buttonSubmit()">
                    Êèê‰∫§
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" slot="append" effect="dark" content="Êí§ÂõûÊúÄËøë‰∏ÄÊ¨°Âà†Èô§ÁöÑÂÜÖÂÆπ" placement="top-end">
                <el-button type="primary" @click="handleReturn()">
                    Êí§Âõû
                </el-button>
            </el-tooltip>
        </el-input>
        <!-- ListÊ°Ü -->
        
        <el-table
            class="list-table"
            :data="dataList">
            <el-table-column
                label="Êó•Êúü"
                width="200px">
                <template slot-scope="dataList">
                    <el-tag size="medium">
                        <i class="el-icon-time"></i>
                        {{ dataList.row.todoTime }}
                    </el-tag>
                </template>
            </el-table-column>
            <el-table-column
                label="ÂÜÖÂÆπ"
                width="auto">
                <template slot-scope="dataList">
                    {{ dataList.row.todoText }}
                </template>
            </el-table-column>
            <el-table-column 
                label="Êìç‰Ωú"
                width="160px">
                <template slot-scope="dataList">
                    <el-button
                        size="mini"
                        @click="handleEdit(dataList.$index, dataList.row)">ÁºñËæë</el-button>
                    <!-- Á°ÆÂÆöÊòØÂê¶Âà†Èô§ -->
                    <el-popover
                        placement="top"
                        width="auto"
                        v-model="dataList.showDel">
                        <p>Á°ÆÂÆöÂà†Èô§ÂêóÔºü</p>
                        <div style="text-align: right; margin: 0">
                            <el-button 
                                type="danger" 
                                size="mini" 
                                @click="handleDelete(dataList.$index, dataList.row),dataList.showDel=flase"
                                >Á°ÆÂÆö</el-button>
                        </div>
                        <el-button class="delSty" type="danger" size="mini" slot="reference">Âà†Èô§</el-button>
                    </el-popover>
                </template>
            </el-table-column>

        </el-table>
        
        
    </div>
</template>

<script>
export default {
    name: 'TodoList',
    data () {
        return {
            inputText: '', //ÊñáÊú¨Ê°ÜÂÜÖÂÆπ
            dataList:[], //List todoText  todoTime
            oldList:{}, //ÈúÄË¶ÅÊí§ÂõûÁöÑList
            oldListIndex: '', //ÈúÄË¶ÅÊí§ÂõûÁöÑListÂùêÊ†á
            
        }
    },
    methods: {
        buttonSubmit () {
            //Êèê‰∫§ÊñáÊú¨
            var _this = this
            let dateTime = _this.Common.getNowTime()//Êó∂Èó¥
            if(_this.inputText == ""){
                return false
            }
            _this.oldList = "" //Ê∏ÖÁ©∫
            _this.oldListIndex = ""
            //Âä†ÂÖ•Êï∞ÊçÆ
            _this.dataList.push({
                todoText: _this.inputText,
                todoTime: dateTime,
                showDel: false, //Á°ÆÂÆöÂà†Èô§ÊòØÂê¶ÊòæÁ§∫
            })

            _this.inputText = "" //Ê∏ÖÁ©∫
        },
        handleEdit(index,row) {
            //ÁºñËæë
            console.log(index,row)
        },
        handleDelete(index,row) {
            //Âà†Èô§
            this.oldList = row //Â≠òÂÖ•ÈúÄË¶ÅÊí§ÂõûÁöÑÊï∞ÁªÑ
            this.oldListIndex = index //Â≠òÂÖ•ÈúÄË¶ÅÊí§ÂõûÁöÑÂùêÊ†á
            this.dataList.splice(index,1) //Âà†Èô§Êï∞ÁªÑ
            this.openVn('Âà†Èô§ÊàêÂäü','success') //ÂºπÂá∫ÊèêÁ§∫

        },
        handleReturn() {
            //Êí§Âõû
            var _this = this
            if(_this.oldListIndex != ""){
                _this.dataList.splice(this.oldListIndex,0,this.oldList) //Êí§ÂõûÊï∞ÁªÑ
                _this.oldList = "" //Ê∏ÖÁ©∫
                _this.oldListIndex = ""
                _this.openVn('Êí§ÂõûÊàêÂäü','success') //ÂºπÂá∫ÊèêÁ§∫
            }else if(_this.oldListIndex == '0'){
                
                _this.dataList.splice(-1,0,this.oldList)//Êí§ÂõûÊï∞ÁªÑ
                _this.oldList = "" //Ê∏ÖÁ©∫
                _this.oldListIndex = ""
                _this.openVn('Êí§ÂõûÊàêÂäü','success') //ÂºπÂá∫ÊèêÁ§∫
            }
            
        },
        openVn(nr,setType) {
            //ÊèêÁ§∫Ê°Ü
            this.$message({
                type: setType,
                center: true,
                dangerouslyUseHTMLString: true,
                message: nr
            })
        }
    }
}
</script>

<style scoped>
    .list-table{
        width: 100%;
        margin-top: 24px;
    }
    .delSty{
        margin-left: 4px;
    }
    .mark{
        line-height: 40px;
        margin-top: 4px;
    }
</style>
