import Vue from "vue";
import VueRouter from "vue-router";

// ÂºïÂÖ•ÁªÑ‰ª∂
import Lgoin from "../components/Pages/Login/Login.vue" //ÁôªÈôÜÈ°µ
import MainIndex from "../components/MainIndex.vue" //‰∏ªË¶ÅÈ°µÈù¢ÔºåÈ¶ñÈ°µ
import Banner from "../components/Pages/Banner/Banner.vue" //ËΩÆÊí≠È¶ñÈ°µ
import TodoListIndex from "../components/Pages/TodoList/TodoListIndex.vue" //TodoList È°µ

